import React from "react";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import TranslateIcon from "@mui/icons-material/Translate";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ModeNightIcon from "@mui/icons-material/ModeNight";
import WbSunnyIcon from "@mui/icons-material/WbSunny";
import { useDispatch, useSelector } from "react-redux";
import { changeTheme } from "../redux/theme";
import { changeLang } from "../redux/lang";
import { changeVisible } from "../redux/visible";
import { Link } from "@material-ui/core";

const Navbar = () => {
  const nightTheme = useSelector((state) => state.theme.value);
  const lang = useSelector((state) => state.lang.value);

  const dispatch = useDispatch();

  const [translateEl, setTranslateEl] = React.useState(null);

  const handleTranslateClick = (event) => {
    setTranslateEl(event.currentTarget);
  };

  const handleTranslateClose = () => {
    setTranslateEl(null);
  };

  const [mainEl, setMainEl] = React.useState(null);

  const handleMainClick = (event) => {
    setMainEl(event.currentTarget);
  };

  const handleMainClose = () => {
    setMainEl(null);
  };

  return (
    <div
      style={{
        borderBottom: nightTheme
          ? `1px solid rgba(255,255,255,0.3)`
          : `1px solid rgba(0,0,0,0.1)`,
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        padding: 10,
      }}
    >
      <Link
        onClick={() => {
          dispatch(changeVisible("home"));
        }}
        href="#"
        style={{
          textDecoration: "none",
          color: "inherit",
          color: nightTheme ? "white" : "#121212",
        }}
      >
        <Typography variant="h6">
          {lang === "en" ? "Danyel Koca" : "コジャ・ダニエル"}
        </Typography>
      </Link>

      <div>
        <IconButton
          edge="start"
          color="inherit"
          aria-label="nightMode"
          onClick={() => dispatch(changeTheme())}
          style={{ marginRight: 10 }}
        >
          {nightTheme ? <WbSunnyIcon /> : <ModeNightIcon />}
        </IconButton>
        <IconButton
          edge="start"
          color="inherit"
          aria-label="translate"
          aria-haspopup="true"
          onClick={handleTranslateClick}
          aria-controls="translate-menu"
          style={{ marginRight: 10 }}
        >
          <TranslateIcon />
        </IconButton>
        <Menu
          id="translate-menu"
          anchorEl={translateEl}
          keepMounted
          open={Boolean(translateEl)}
          onClose={handleTranslateClose}
        >
          <MenuItem
            disabled={lang === "en" ? true : false}
            onClick={function () {
              handleTranslateClose();
              dispatch(changeLang(lang == "en" ? "jp" : "en"));
            }}
          >
            {lang === "en" ? "English" : "英語"}
          </MenuItem>
          <MenuItem
            disabled={lang === "jp" ? true : false}
            onClick={function () {
              handleTranslateClose();
              dispatch(changeLang(lang == "en" ? "jp" : "en"));
            }}
          >
            {lang === "en" ? "Japanese" : "日本語"}
          </MenuItem>
        </Menu>
        <IconButton
          edge="start"
          color="inherit"
          aria-label="main menu"
          aria-haspopup="true"
          onClick={handleMainClick}
          aria-controls="main-menu"
        >
          <MenuIcon />
        </IconButton>
        <Menu
          id="main-menu"
          anchorEl={mainEl}
          keepMounted
          open={Boolean(mainEl)}
          onClose={handleMainClose}
        >
          <MenuItem onClick={handleMainClose}>
            <Link
              onClick={() => {
                dispatch(changeVisible("home"));
              }}
              href="#my-works"
              style={{ textDecoration: "none", color: "black" }}
            >
              {lang === "en" ? "My works" : "プロジェクトの事例"}
            </Link>
          </MenuItem>
          <MenuItem onClick={handleMainClose}>
            <Link
              onClick={() => {
                dispatch(changeVisible("home"));
              }}
              href="#about"
              style={{ textDecoration: "none", color: "black" }}
            >
              {lang === "en" ? "My story" : "学・経歴"}
            </Link>
          </MenuItem>
          <MenuItem onClick={handleMainClose}>
            <Link
              onClick={() => {
                dispatch(changeVisible("home"));
              }}
              href="#blog"
              style={{ textDecoration: "none", color: "black" }}
            >
              {lang === "en" ? "Blog" : "ブログ"}
            </Link>
          </MenuItem>

          <MenuItem onClick={handleMainClose}>
            <Link
              onClick={() => {
                dispatch(changeVisible("home"));
              }}
              href="#contact"
              style={{ textDecoration: "none", color: "black" }}
            >
              {lang === "en" ? "Contact" : "コンタクト"}
            </Link>
          </MenuItem>
        </Menu>
      </div>
    </div>
  );
};

export default Navbar;
