import { Typography } from "@material-ui/core";
import { useSelector } from "react-redux";

function getColor(opacity) {
  return `hsla(${360 * Math.random()}, ${25 + 70 * Math.random()}%, ${
    85 + 10 * Math.random()
  }%, ${opacity})`;
}
const Item = ({ img, text, date, index }) => {
  const nightTheme = useSelector((state) => state.theme.value);

  return (
    <div
      className={nightTheme ? "item itemNight" : "item"}
      style={{
        backgroundColor: nightTheme
          ? "rgba(255,255,255,0.02)"
          : "rgba(0,0,0,0.02)",
      }}
    >
      <span className={nightTheme ? "date dateNight" : "date"}>{date}</span>
      <img src={img} alt={text} />
      <Typography variant="subtitle2">{text}</Typography>
    </div>
  );
};

export default Item;
