import { createTheme } from "@material-ui/core";
import ThemeProvider from "@material-ui/styles/ThemeProvider";
import Navbar from "./components/navbar";
import MainDisplay from "./components/mainDisplay";
import WorksDisplay from "./components/worksDisplay";
import { CSSTransition } from "react-transition-group";
import { useSelector, useDispatch } from "react-redux";
import { changeLocal } from "./redux/local";
import React, { useState, useEffect } from "react";
import axios from "axios";

const theme = createTheme({
  palette: {
    primary: {
      main: "rgb(70,130,180)",
    },
    breakpoints: {
      values: {
        xs: 0,
        sm: 600,
        md: 1000,
        lg: 1200,
        xl: 1536,
      },
    },
  },
});


function App() {
  const nightTheme = useSelector((state) => state.theme.value);
  const visible = useSelector((state) => state.visible.value);
  const local = useSelector((state) => state.local.value);

  const dispatch = useDispatch();


  useEffect(() => {
    getGeoInfo();
  }, []);

  const getGeoInfo = () => {
    axios
      .get("https://ipapi.co/json/")
      .then((response) => {
        let data = response.data;
        dispatch(
          changeLocal({
            ip: data.ip,
            countryName: data.country_name,
            countryCode: data.country_calling_code,
            city: data.city,
            timezone: data.timezone,
          })
        );
      })
      .catch((error) => {
        console.log(error);
      });
  };


  return (
    <ThemeProvider theme={theme} className="noScroll">
      <div
        className="noScroll"
        style={{
          display: "flex",
          justifyContent: "center",
          flexGrow: 1,
          backgroundColor: nightTheme ? "#121212" : "white",
          color: nightTheme ? "white" : "#121212",
        }}
      >
        <div
          style={{
            maxWidth: 1000,
            width: "100%",
            fexGrow: 1,
            minHeight: "100vh",
            position: "relative",
          }}
        >
          <Navbar />
          <div style={{ position: "relative" }}>
            <CSSTransition
              in={visible === "home"}
              timeout={200}
              classNames="page"
              unmountOnExit
            >
              <div className="page">
                <MainDisplay />
              </div>
            </CSSTransition>
            <CSSTransition
              in={visible === "works"}
              timeout={200}
              classNames="page"
              unmountOnExit
            >
              <div className="page">
                <WorksDisplay />
              </div>
            </CSSTransition>
          </div>
        </div>
      </div>
    </ThemeProvider>
  );
}

export default App;
